import React, { Fragment } from "react";
import { Hidden } from "@mui/material";

import LeadDistributionContainer from "../../../Container/BusinessSettings/LeadDistribution";
import LeadDistributionForm from "../../../Container/BusinessSettings/LeadDistribution/LeadForm";
import { useLeadDistributionRules } from "../../../contexts/LeadDistributionRulesProvider";

import Layout from "../Layout";

const LeadDistributionRules = () => {
  const { showCreateForm } = useLeadDistributionRules();

  return (
    <Fragment>
      <Hidden smUp implementation="js">
        <LeadDistributionContainer />
        {showCreateForm && <LeadDistributionForm />}
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <LeadDistributionContainer />
          {showCreateForm && <LeadDistributionForm />}
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default LeadDistributionRules;
