import { Button } from "@material-ui/core";
import { Hidden } from "@mui/material";

import leftArrow from "../../../../assets/icons/leftArrow.svg";
import { useLeadDistributionRules } from "../../../../contexts/LeadDistributionRulesProvider";

const LeadHeader = ({ leadsCount }) => {
  const { toggleCreateForm } = useLeadDistributionRules();

  return (
    <div className="header">
      <Hidden xsDown implementation="js" only="xs">
        <div className="top">
          <Button
            variant="contained"
            type="button"
            className="btn-action"
            onClick={() => toggleCreateForm(true)}
          >
            {"New Lead Distribution Rule"}
          </Button>
        </div>
      </Hidden>
      <div className="below">
        <div className="left">
          <Hidden smUp implementation="js">
            <a href="/business-settings">
              <img src={leftArrow} style={{ margin: "10px" }} alt="back" />
            </a>
          </Hidden>
          <h2>{`${leadsCount} Lead Distribution Rules`}</h2>
        </div>
      </div>
    </div>
  );
};

export default LeadHeader;
