/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment, useCallback, useEffect, useState } from "react";
import { Divider, Button } from "@mui/material";
import { Hidden } from "@material-ui/core";
import { LoadingButton } from "@mui/lab";

import { useLeadDistributionRules } from "../../../contexts/LeadDistributionRulesProvider";
import leftArrow from "../../../assets/icons/leftArrow.svg";
import { useToastMessage } from "../../../contexts/ToastMessageContext";
import { getRules } from "../../../api/LMSRules";

import LeadHeader from "./LeadHeader";
import LeadFooter from "./LeadFooter";
import LeadListItem from "./LeadListItem";
import useStyles from "./styles/muiStyles";
import { Box } from "@mui/material";

const LeadDataFieldContainer = () => {
  const classes = useStyles();
  const [loading, setLoading] = useState(true);
  const [loadingMore, setLoadingMore] = useState(false);
  const {
    allLeadRules,
    setAllLeadRules,
    reloadRules,
    doReloadRules,
    toggleCreateForm,
  } = useLeadDistributionRules();
  const [totalRules, setTotalRules] = useState();
  const [currentPage, setCurrentPage] = useState(1);
  const { setErrorMessage } = useToastMessage();
  const loadRules = useCallback(async () => {
    if (totalRules && allLeadRules.length === totalRules) {
      setLoadingMore(false);
      return;
    }
    try {
      const rulesResponse = await getRules(currentPage);
      if (!rulesResponse.success) {
        throw Error(rulesResponse.message);
      } else {
        typeof totalRules === "undefined" &&
          setTotalRules(rulesResponse.payload.meta.total);
        setAllLeadRules((prevLeadRules) =>
          prevLeadRules.concat(rulesResponse.payload.data)
        );
      }
    } catch (error) {
      setErrorMessage(error.message);
    } finally {
      setLoadingMore(false);
      setLoading(false);
    }
  }, [currentPage]);

  const doReload = useCallback(async () => {
    try {
      const rulesResponse = await getRules(1);
      setTotalRules(rulesResponse.payload.meta.total);
      setAllLeadRules(rulesResponse.payload.data);
    } catch (error) {
      setErrorMessage(error.message);
    } finally {
      doReloadRules(false);
    }
  }, []);

  useEffect(() => {
    if (reloadRules) {
      doReload();
    }
  }, [doReload, reloadRules]);

  useEffect(() => {
    loadRules();
  }, [loadRules]);

  return (
    <Fragment>
      {/* Mobile View */}
      <Hidden smUp implementation="js">
        <div className={classes["mobile-settings"]}>
          {allLeadRules && allLeadRules.length === 0 && (
            <div className="header">
              <a href="/business-settings" className="back-link">
                <img alt="back" src={leftArrow} />
              </a>
              <h1 className="title">{"Lead Distribution Rules"}</h1>
            </div>
          )}
          <div className="content">
            <div className="lead-container">
              {allLeadRules && allLeadRules.length === 0 ? (
                <div className="default-block">
                  <div className="cta">
                    <Button
                      variant="contained"
                      type="button"
                      className="btn-action"
                      onClick={() => toggleCreateForm(true)}
                    >
                      {"New Lead Distribution Rule"}
                    </Button>
                  </div>
                  <div className="info">
                    <p>
                      {
                        "Create logical rules, based on which an enquiry is automatically assigned to a team member."
                      }
                    </p>
                  </div>
                </div>
              ) : (
                <div className="card-group">
                  <LeadHeader leadsCount={allLeadRules?.length ?? 0} />
                  <Divider className="full" />
                  <div className="card-content">
                    {allLeadRules &&
                      allLeadRules.map((e, i) => (
                        <LeadListItem
                          key={i}
                          name={e.user.name}
                          code={e.rule.value}
                          Id={e.id}
                        />
                      ))}
                    <Box display="flex" justifyContent="center">
                      <LoadingButton
                        variant="contained"
                        color="primary"
                        onClick={() => {
                          setCurrentPage((currentPage) => currentPage + 1);
                          setLoadingMore(true);
                        }}
                        loading={loadingMore}
                        sx={{
                          bgcolor: "#12b0ee",
                          textTransform: "capitalize",

                          "&:hover": { bgcolor: "#12b0ee" },
                        }}
                      >
                        Load More
                      </LoadingButton>
                    </Box>
                  </div>
                  <LeadFooter />
                </div>
              )}
            </div>
          </div>
        </div>
      </Hidden>
      {/* Table/Desktop View */}
      <Hidden xsDown implementation="js" only="xs">
        {!loading && (
          <div className={classes["lead-container"]}>
            {allLeadRules.length === 0 ? (
              <div className="card-group default">
                <div className="inner-block">
                  <div className="cta">
                    <Button
                      variant="contained"
                      type="button"
                      className="btn-action"
                      onClick={() => toggleCreateForm(true)}
                    >
                      {"New Lead Distribution Rule"}
                    </Button>
                  </div>
                  <div className="info">
                    <p>
                      {
                        "Create logical rules, based on which an enquiry is automatically assigned to a team member."
                      }
                    </p>
                  </div>
                </div>
              </div>
            ) : (
              <div className="card-group">
                <LeadHeader leadsCount={totalRules} />
                <Divider />
                <div className="card-content">
                  {allLeadRules &&
                    allLeadRules.map((e, i) => (
                      <LeadListItem
                        key={i}
                        name={e.user.name}
                        code={e.rule.value}
                        Id={e.id}
                      />
                    ))}
                  <Box display="flex" justifyContent="center">
                    <LoadingButton
                      variant="contained"
                      color="primary"
                      onClick={() => {
                        setCurrentPage((currentPage) => currentPage + 1);
                        setLoadingMore(true);
                      }}
                      loading={loadingMore}
                      sx={{
                        bgcolor: "#12b0ee",
                        textTransform: "capitalize",

                        "&:hover": { bgcolor: "#12b0ee" },
                      }}
                    >
                      Load More
                    </LoadingButton>
                  </Box>
                </div>
              </div>
            )}
          </div>
        )}
      </Hidden>
    </Fragment>
  );
};

export default LeadDataFieldContainer;
