/* eslint-disable react-hooks/exhaustive-deps */
import React, {
  Fragment,
  useCallback,
  useEffect,
  useRef,
  useState,
} from "react";
import {
  Chip,
  Menu,
  MenuItem,
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
  ListItem,
  ListItemText,
  ListItemIcon,
  Skeleton,
} from "@mui/material";
import { IconButton } from "@material-ui/core";
import { MoreVert } from "@mui/icons-material";

import { useLeadDistributionRules } from "../../../../contexts/LeadDistributionRulesProvider";
import { useToastMessage } from "../../../../contexts/ToastMessageContext";
import useOnScreen from "../../../../hooks/useOnScreen";
import { deleteRule } from "../../../../api/LMSRules";
import useStyles from "../styles/muiStyles";

const LeadListItem = ({
  name,
  code,
  Id,
  last = false,
  loadedRules,
  totalRules,
  loadingMore,
  setLoadingMore,
  setCurrentPage,
}) => {
  const classes = useStyles();
  const endLeadRule = useRef();
  const visible = useOnScreen(endLeadRule, "0px", 1);
  //contexts
  const { reloadRules, doReloadRules } = useLeadDistributionRules();
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  // 3dot menu state
  const [anchorEl, setAnchorEl] = useState(null);
  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  //function for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };
  //function for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };
  //function for opening 3 dot menu
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  //function for closing 3 dot menu
  const handleClose = () => {
    setAnchorEl(null);
  };
  //function deleteing team member
  const deleteLeadRule = async () => {
    try {
      const response = await deleteRule(Id);
      setSuccessMessage(response.message);
      doReloadRules(true);
    } catch (err) {
      setErrorMessage(err.message);
    } finally {
      handleClickClose();
    }
  };

  const ifEndVisible = useCallback(() => {
    if (!reloadRules) {
      if (visible && loadedRules < totalRules) {
        setLoadingMore(true);
        setCurrentPage((currentPage) => currentPage++);
      }
    }
  }, [reloadRules, loadedRules, totalRules, visible]);

  useEffect(() => {
    ifEndVisible();
  }, [ifEndVisible]);

  return last ? (
    <div className="end-area" ref={endLeadRule}>
      {loadingMore && (
        <ListItem component="div">
          <ListItemText
            primary={
              <div className="primary-block">
                <h2 className={"list-title"}>
                  <Skeleton variant="text" width={110} />
                </h2>
                <Skeleton
                  variant="text"
                  sx={{ bgcolor: "#12B0EE" }}
                  width={55}
                />
              </div>
            }
          />
        </ListItem>
      )}
    </div>
  ) : (
    <Fragment>
      <ListItem component="div">
        <ListItemText
          primary={
            <div className="primary-block">
              <h2 className={"list-title"}>{name}</h2>
              <Chip
                label={code}
                className={"status-chip"}
                size="small"
                style={{
                  backgroundColor: "#12B0EE",
                  alignItems: "center",
                }}
              />
            </div>
          }
        />
        <ListItemIcon>
          <IconButton
            // disabled
            onClick={handleClick}
          >
            <MoreVert />
          </IconButton>
          {Boolean(anchorEl) && (
            <Menu
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={handleClose}
              keepMounted
              MenuListProps={{
                "aria-labelledby": "Options",
              }}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              className={classes["item-options"]}
            >
              <MenuItem
                onClick={() => {
                  handleClickOpen();
                  handleClose();
                }}
              >
                {"Delete"}
              </MenuItem>
            </Menu>
          )}
        </ListItemIcon>
      </ListItem>
      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">{"Delete"}</DialogTitle>
        <DialogContent>{"Are you sure you want to delete?"}</DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              handleClickClose();
            }}
          >
            {"Cancel"}
          </Button>
          <Button
            onClick={() => {
              deleteLeadRule();
            }}
            autoFocus
          >
            {"Delete"}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
};

export default LeadListItem;
