/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-unreachable */
import { Fragment, forwardRef, useCallback, useEffect, useState } from "react";
import {
  IconButton,
  Autocomplete,
  CircularProgress,
  MenuItem,
  TextField,
} from "@mui/material";

import { useDebounce } from "hooks/useDebounce";
import { UIButton } from "Components/exports";
import {
  // CustomInputField,
  CustomTextFieldFilled,
} from "Components/UI/CustomTextField/CustomTextField";
import leftArrow from "assets/icons/leftArrow.svg";
import { useToastMessage } from "contexts/ToastMessageContext";
import { useLeadDistributionRules } from "contexts/LeadDistributionRulesProvider";
import { getLeadDataFields, getTeamMembers, createNewRule } from "api/LMSRules";

import useStyles from "./styles/muiStyles";

const ListBox = forwardRef(({ children, ...rest }, ref) => (
  // eslint-disable-next-line jsx-a11y/aria-role
  <ul {...rest} role="list-box">
    {children}
  </ul>
));

const LeadDistributionForm = () => {
  const classes = useStyles();
  const { toggleCreateForm, doReloadRules } = useLeadDistributionRules();
  const { setErrorMessage, setSuccessMessage } = useToastMessage();
  const [errorFocus, setErrorFocus] = useState(0);
  const [showForm, setShowForm] = useState(false);
  const [creating, setCreating] = useState(false);
  const [leadDataFields, setLeadDataFields] = useState();
  const [selectedField, setSelectedField] = useState("");
  const [selectedType, setSelectedType] = useState("");
  const [matchValue, setMatchValue] = useState();
  const [members, setMembers] = useState([]);
  const [membersTotal, setMembersTotal] = useState();
  const [showMembers, toggleMembers] = useState(false);
  const [loadingMembers, setLoadingMembers] = useState(true);
  const [memberSearch, setMemberSearch] = useState("");
  const deferredMSearch = useDebounce(memberSearch);
  const [membersIndex, setMembersIndex] = useState(1);
  const [selectedUser, setSelectedUser] = useState("");

  const doLoadMore = async (teamMembersIndex) => {
    try {
      setMembersIndex(teamMembersIndex);
      const res = await getTeamMembers(deferredMSearch, membersIndex);
      if (!res.success) {
        throw Error(res.message);
      }
      setMembers([...members, ...res.payload.data]);
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  const submit = async (e) => {
    try {
      e.preventDefault();
      setErrorFocus(0);
      setCreating(true);
      const response = await createNewRule({
        param_name: selectedField,
        rule: { value: matchValue },
        assign_to: selectedUser,
        param_type: "string",
        logic: "match",
      });

      if (!response.success) {
        let erroMsg = response.messages
          ? response.messages.errors[0].message
          : response.message;
        if (erroMsg.includes("param_name")) {
          setErrorFocus(1);
          throw new Error(erroMsg.replace("param_name", "Lead Data Field"));
        } else if (erroMsg.includes("rule.value")) {
          setErrorFocus(2);
          throw new Error(erroMsg.replace("rule.value", "Match Value"));
        } else if (erroMsg.includes("assign_to")) {
          setErrorFocus(3);
          throw new Error(erroMsg.replace("assign_to", "Assign To"));
        } else {
          throw new Error(response.error[0].message);
        }
      } else {
        setSuccessMessage(response.message);
        doReloadRules(true);
        setShowForm(false);
        setTimeout(() => toggleCreateForm(false), 200);
      }
    } catch (error) {
      setErrorMessage(error.message);
    } finally {
      setCreating(false);
    }
  };

  const fetchLeadDataFields = useCallback(async () => {
    const response = await getLeadDataFields();
    console.log("response", response);
    response && setLeadDataFields(response.payload);
  }, []);

  const fetchTeamMembers = useCallback(async () => {
    try {
      if (showForm) {
        const startPage = 1;
        setLoadingMembers(true);
        setMembersIndex(startPage);
        const res = await getTeamMembers(deferredMSearch, startPage);
        if (!res.success) {
          throw Error(res.message);
        }
        setMembers(res.payload.data);
        setMembersTotal(res.payload.meta.total);
        setLoadingMembers(false);
      }
    } catch (error) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showForm, deferredMSearch]);

  useEffect(() => {
    fetchLeadDataFields();
  }, [fetchLeadDataFields]);

  useEffect(() => {
    fetchTeamMembers();
  }, [fetchTeamMembers]);

  useEffect(() => {
    setTimeout(() => setShowForm(true), 200);
  }, []);

  false && console.log(selectedType);

  return (
    <Fragment>
      <div className={classes.overlay} />
      <div className={`${classes["lead-form"]}${showForm ? " active" : ""}`}>
        <Fragment>
          <div className="header">
            <IconButton
              aria-label="Back"
              className="back-btn"
              onClick={() => {
                setShowForm(false);
                setTimeout(() => toggleCreateForm(false), 200);
              }}
              style={{ color: "transparent", padding: 0 }}
            >
              <img
                alt="back"
                src={leftArrow}
                style={{ margin: "7.5px 10px" }}
              />
            </IconButton>
            <h1 className="title">{"Create New Rule"}</h1>
          </div>
          <form onSubmit={(e) => submit(e)} autocomplete="off">
            <p className="from-intro">
              {
                "Assign enquiry to team member when lead data field matches value."
              }
            </p>
            <div className="field">
              <CustomTextFieldFilled
                select
                placeholder="Select Lead Data Field"
                label="Lead Data Field"
                variant="filled"
                type="select"
                fullWidth
                name="leadDataField"
                className={errorFocus === 1 ? "has-error" : ""}
                defaultValue={selectedField}
                onChange={({ target: { value } }) => {
                  setSelectedType(
                    leadDataFields
                      .filter(({ field_name }) => value === field_name)
                      .pop().field_type
                  );
                  setSelectedField(value);
                }}
                autoFocus={errorFocus === 1}
              >
                {(typeof leadDataFields === "undefined" ||
                  leadDataFields.length === 0) && (
                  <MenuItem value={""} disabled>
                    {"No Lead Data Field Available"}
                  </MenuItem>
                )}
                {leadDataFields &&
                  leadDataFields.map(({ id, display_name, field_name }) => (
                    <MenuItem key={id} value={field_name}>
                      {display_name}
                    </MenuItem>
                  ))}
              </CustomTextFieldFilled>
            </div>
            <div className="field">
              <CustomTextFieldFilled
                label="Match Value"
                placeholder="Match Value"
                name="matchValue"
                className={errorFocus === 2 ? "has-error" : ""}
                inputRef={(input) => errorFocus === 2 && input && input.focus()}
                value={matchValue}
                onChange={({ target: { value } }) => setMatchValue(value)}
              />
            </div>
            <div className="field">
              <Autocomplete
                open={showMembers}
                onOpen={() => {
                  toggleMembers(true);
                }}
                onClose={() => {
                  setMemberSearch("");
                  toggleMembers(false);
                }}
                noOptionsText={"Team Member not found."}
                isOptionEqualToValue={(option, value) =>
                  option.user.name === value.user.name
                }
                getOptionLabel={(option) => option.user.name}
                options={members}
                loading={loadingMembers}
                onChange={(_event, newValue) => {
                  newValue && setSelectedUser(newValue.user.id);
                }}
                onInputChange={(_event, newInputValue) => {
                  setMemberSearch(
                    newInputValue.length > 2 ? newInputValue : ""
                  );
                }}
                ListboxComponent={ListBox}
                ListboxProps={{
                  onScroll: (event) => {
                    const listboxNode = event.currentTarget;
                    if (
                      listboxNode.scrollTop + listboxNode.clientHeight ===
                      listboxNode.scrollHeight
                    ) {
                      if (membersTotal) {
                        if (membersTotal > members.length) {
                          doLoadMore(membersIndex + 1);
                        }
                      }
                    }
                  },
                }}
                renderOption={(props, option) => {
                  return (
                    <MenuItem
                      {...props}
                      value={option.user.id}
                      key={option.user_id}
                    >
                      {option.user.name}
                    </MenuItem>
                  );
                }}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="Assign To"
                    variant="filled"
                    required={true}
                    InputProps={{
                      ...params.InputProps,
                      sx: {
                        bgcolor: "#F2F3F7",
                        borderRadius: 2,

                        "&.MuiFilledInput-root": {
                          pl: 1.75,
                          height: "100%",

                          "& .MuiInputBase-input.MuiFilledInput-input": {
                            p: 0,
                          },
                        },
                      },
                      endAdornment: (
                        <Fragment>
                          {loadingMembers ? (
                            <CircularProgress color="inherit" size={20} />
                          ) : null}
                          {params.InputProps.endAdornment}
                        </Fragment>
                      ),
                    }}
                  />
                )}
              />
            </div>
            <div className="actions">
              <UIButton type="submit" className="ms-0" disabled={creating}>
                {"Save"}
              </UIButton>
            </div>
          </form>
        </Fragment>
      </div>
    </Fragment>
  );
};

export default LeadDistributionForm;
